import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
  KeyboardEvent,
  ReactNode,
} from 'react'

import style from './SuperInputText.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = Omit<DefaultInputPropsType, 'type'> & {
  //  +       
  onChangeText?: (value: string) => void
  onEnter?: () => void
  error?: ReactNode
  spanClassName?: string
  type: string
}

export const SuperInputText: React.FC<SuperInputTextPropsType> = ({
  onChange,
  onChangeText,
  onKeyDown,
  onEnter,
  error,
  className,
  spanClassName,
  id,
  type,

  ...restProps //       restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange?.(e) //    onChange,     ( onChange  )

    onChangeText?.(e.currentTarget.value)
  }
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyDown?.(e)

    onEnter && //    onEnter
      e.key === 'Enter' && //     Enter
      onEnter() //   
  }

  const finalSpanClassName = style.error + (spanClassName ? ' ' + spanClassName : '')
  const finalInputClassName =
    style.input +
    (error ? ' ' + style.errorInput : ' ' + style.superInput) +
    (className ? ' ' + style.className : '')

  return (
    <div className={style.inputWrapper}>
      <input
        id={id}
        type={type}
        onChange={onChangeCallback}
        onKeyDown={onKeyPressCallback}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
      <span id={id ? id + '-span' : undefined} className={finalSpanClassName}>
        {error}
      </span>
    </div>
  )
}
